.single-issue-container__open {
  margin: 1rem 0;
  background: rgba(#a01c07, 0.5);
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  border: 2px solid firebrick;
}
.single-issue-container__closed {
  position: relative;
  background: rgba(rgb(150, 112, 112), 0.5);
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  margin: 1rem 0;
  border: 2px solid rgb(150, 112, 112);
  .issue-component-overlay {
    position: absolute;
    width: 100%;
    height: 310px;
    z-index: 2;
    color: firebrick;
    font-size: 3rem;
    background: rgba(#f1f1f1, 0.8);
    display: flex;
    align-items: center;
    justify-content: center;
    .overlay-txt {
      font-weight: 300;
      transform: rotate(-30deg);
    }
  }
}

.single-issue-header {
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  height: 50px;
}
.single-issue-body {
  font-size: 1.2rem;
  height: 150px;
  width: 100%;
  border-top: 1px solid black;
  border-bottom: 1px solid black;
  align-items: flex-start;
}
.single-issue-footer {
  height: 100px;
  display: flex;
  align-items: flex-end;
  justify-content: space-between;
  .single-issue-status__footer {
    align-self: flex-end;

    margin: 0;
  }
  * {
    padding-bottom: 10px;
    flex-basis: 30%;
  }
  h1 {
    align-self: flex-end;
  }
}
